<template>
  <form @submit.prevent="doLogin()" class="form-login">
    <div class="card text-center">
      <h1>CinePrjct</h1>
      <div class="card-body">
        <div class="form-group">
          <input required type="email" class="form-control" placeholder="E-mail" v-model="email"/>
        </div>
        <div class="form-group">
          <input required type="password" class="form-control" placeholder="Senha" v-model="senha"/>
        </div>
        <button class="btn btn-primary w-100" :disabled="loading">
          Entrar
        </button>
        <!--<p @click="cadastro">Não possui Cadastro?</p>-->
        <cadastro/>
      </div>
    </div>
  </form>
</template>

<script>
import '../../firebase/index'
import firebase from 'firebase'
import Cadastro from '../Cadastro/Cadastro'
export default {
  components: { Cadastro },
  name: 'Login',
  data: () => {
    return {
      loading: false,
      email: '', // jose@gmail.com
      senha: '' // 123456
    }
  },
  methods: {
    async doLogin () {
      this.loading = true
      try {
        // const res = await this.$firebase.auth().signInWithEmailAndPassword(email, senha)
        const res = await firebase.auth().signInWithEmailAndPassword(this.email, this.senha)
        window.uid = res.user.uid

        this.$router.push({ name: 'Home' })

        console.log(res)
      } catch (err) {
        console.log(err)
      }
      this.loading = false
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      if (window.uid) {
        vm.$router.push({ name: 'Home' })
      }
    })
  },
  cadastro () {
    alert('cadastro')
    this.$router.push({ name: 'Cadastro' })
  }
}
</script>

<style lang="scss" scoped>
.form-login {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  .card {
    width: 25%;
  }
}
</style>
